body {
  background-color: #0084cd;
}
#title {
  text-align: center;
}
#recipeList {
  border: 2px solid black;
  overflow: scroll;
  height: 20vh;
  border-radius: 10px;
}
#recipeDisplay {
  border: 2px solid black;
  margin: 5px;
  border-radius: 10px;
  overflow: scroll;
  height: 70vh;
}

#dialog {
  text-align: center;
  position: fixed;
  width: 500px;
  min-width: 300px;
  top: 50%;
  left: 50%;
  margin-left: -250px;
  margin-top: -200px;
  box-shadow: 3px 3px 10px black;
  border-radius: 15px;
  background: #bfc0bf;
  z-index: 1000;
  .input-title {
    margin: 10px;
  }
  textarea {
    margin-bottom: 10px;
    background: #556670;
    min-width: 300px;
    min-height: 60px;
    resize: vertical;
    color: darken($titles, 10%);
    font-weight: bold;
    font-size: 13px;
    overflow: auto;
    &:nth-of-type(3) {
      min-height: 100px;
    }
    &:first-of-type {
      resize: none;
      min-height: 15px !important;
    }
  }
}
